/* App.css */
@font-face {
  font-family: "Freesentation-9Black";
  src: url("https://fastly.jsdelivr.net/gh/projectnoonnu/2404@1.0/Freesentation-9Black.woff2")
    format("woff2");
  font-weight: 900;
  font-style: normal;
}

* {
  font-family: "Freesentation-9Black";
}

div {
  box-sizing: border-box;
}

.title-input {
  width: 10rem;
  padding: 6px;
  border-radius: 10px;
  border: 2px solid rgb(197, 197, 197);
}

.content-input {
  width: 10rem;
  padding: 6px;
  height: 3rem;
  border-radius: 10px;
  border: 2px solid rgb(197, 197, 197);
}

.black-nav {
  background-color: black;
  color: white;
  padding: 0.6rem;
}

.black-nav h1 {
  margin: 0.8rem;
}

.post-btn {
  width: 15rem;
  padding: 6px 12px;
  border-radius: 10px;
  border: none;
}

.post-container {
  display: flex;
  flex-direction: column;
  padding-bottom: 61px;
}

.input-container {
  display: flex;
  align-items: center;
  gap: 10px;
}

.post-btn-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 16px;
  gap: 10px;
  box-shadow: 0 10px 10px rgb(235, 235, 235);
}

.sort-btn-container {
  margin: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  gap: 16px;
  border: 2px solid rgb(10, 87, 50);
  border-radius: 26px;
}

.sort-btn {
  padding: 6px 12px;
  border: none;
  border-radius: 20px;
  color: white;
  background-color: rgb(10, 87, 50);
}

.modify-btn {
  padding: 6px 12px;
  width: 15rem;
  border: none;
  border-radius: 20px;
  color: white;
  background-color: rgb(62, 43, 205);
}

.delete-btn {
  border: none;
  border-radius: 20px;
  color: white;
  background-color: rgb(170, 0, 0);
}
.list {
  text-align: left;
  padding: 20px;
  border-bottom: 1px solid grey;
}

.list h4 {
  margin: 0;
}

.modal {
  position: fixed;
  width: 20rem;
  height: 23.4rem;
  top: calc(50vh - 12rem);
  left: calc(50vw - 10rem);
  margin-top: 20px;
  padding: 20px;
  background: #eee;
  text-align: left;
}
.modal-xbtn-container {
  display: flex;
  width: 100%;
  height: 10px;
  justify-content: end;
}

.modal-xbtn-container p {
  margin: 0;
}

.modify-btn-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: black;
  color: white;
  padding: 0.6rem;
}

.footer p {
  margin: 0;
}
